/**
 * Common validation regexes
 */

module.exports = {

  Patterns: {
    email: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
    phone: /^(?:\+|00)\d{1,2}\s*\d{3}\s*\d{4}\s*\d{3}$/
  }

};



